<template>
  <div class="ac-esign-thumbnails" :style="thumbnails_styles">
    <slot v-if="!has_pages" name="loader" />

    <b-form class="mb-3" inline v-if="has_pages" @submit.prevent.stop="go_to($refs.input.$el.value)">
      <b-input size="sm" type="number" class="mb-2 mr-sm-2 mb-sm-0 w-25" ref="input" placeholder="Page" :value="active_page"></b-input>
      <label class="mr-sm-2">Showing {{ active_page }} of {{ pages.length }}</label>
    </b-form>

    <AcEsignThumbnail
      v-for="page in pages"
      :key="`page_${page._pageIndex}`"
      :width="thumbnail_width"
      :pdfjs_page="page"
      :active_class="active_page === page._pageIndex + 1"
      class="ac-esign-thumbnails_item"
      @click.native="go_to(page._pageIndex + 1)"
    >
      <span>{{ page._pageIndex + 1 }}</span>
    </AcEsignThumbnail>
  </div>
</template>

<script>
  import AcEsignThumbnail from "./AcEsignThumbnail";

  /**
   * Ac Esign Thumbnails - list of pdf document thumbnails
   * @requires AcEsignThumbnail
   *
   * @event go_to
   * @param {number} page_index - clicked thumbnail
   */
  export default {
    name: "AcEsignThumbnails",

    components: {
      AcEsignThumbnail,
    },

    props: {
      // Instance of PDF.js - loaded pdf document
      pdfjs_document: {
        type: Object,
        required: true,
      },

      active_page: Number,

      thumbnail_width: {
        type: Number,
        default: 150,
      },
    },

    data() {
      return {
        pages: [],
      };
    },

    computed: {
      has_pages() {
        return this.pages.length > 0;
      },

      thumbnails_styles() {
        return {
          minWidth: `${this.thumbnail_width + 20}px`,
          width: `${this.thumbnail_width + 20}px`,
        };
      },
    },

    watch: {
      pdfjs_document() {
        this.set_pages();
      },
    },

    methods: {
      /**
       * Get pages instance from pdfjs document
       */
      async set_pages() {
        const pages_promise = [];

        for (let i = 1; i <= this.pdfjs_document.numPages; i++) {
          pages_promise.push(this.pdfjs_document.getPage(i));
        }

        Promise.all(pages_promise).then((pages) => {
          this.pages = pages;
        });
      },

      /**
       * Triggered by on click thumbnail
       * @param {number} page_index
       */
      go_to(page_index) {
        if (page_index > this.pages.length) return;
        this.$emit("go_to", parseInt(page_index, 10));
      },
    },
  };
</script>
<style scoped>
  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type="number"] {
    -moz-appearance: textfield;
    background-color: rgba(255, 255, 255, 0.09);
    color: #12263f;
    text-align: right;
    border-width: 1px;
    border-style: solid;
    border-color: #d2ddec;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05) inset, 0 1px 0 rgba(255, 255, 255, 0.05);
    padding: 0.4rem;
  }
  input[type="number"]:focus {
    border-color: #2c7be5;
  }

  .ac-esign-thumbnails {
    max-height: 100vh;
    overflow-y: auto;
  }

  .ac-esign-thumbnails_item {
    cursor: pointer;
    position: relative;
    margin: 5px;
    margin-top: 15px !important;
  }
  .ac-esign-thumbnails_item.active:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% + 5px);
    z-index: -1;
    height: calc(100% + 5px);
    transform: translate(-2px, -2px);
    background-clip: padding-box;
  }
  .ac-esign-thumbnails_item + .ac-esign-thumbnails_item {
    margin-top: 0.5rem;
  }
  .w-25 {
    width: 25% !important;
  }
  input[type="number"]::-webkit-input-placeholder {
    font-size: 12px;
  }
</style>
