<template>
  <div class="ac-esign-thumbnail">
    <div class="ac-esign-thumbnail_index">
      <slot />
    </div>

    <canvas ref="canvas" :class="active_class ? 'border border-primary' : ''"></canvas>
  </div>
</template>
<script>
  /**
   * Ac Esign Thumbnail - view of pdf page
   *
   * @event rendered - emits by rendered page
   */
  export default {
    name: "AcEsignThumbnail",

    props: {
      pdfjs_page: {
        type: Object,
        required: true,
      },
      active_class: {
        type: Boolean,
        default: false,
      },

      width: Number,
    },

    mounted() {
      this.render_page();
    },

    methods: {
      render_page() {
        const page_viewport = this.pdfjs_page.getViewport({ scale: 1 });
        const scale = this.width / page_viewport.width;
        const scaled_viewport = this.pdfjs_page.getViewport({ scale });

        this.$refs.canvas.width = scaled_viewport.width;
        this.$refs.canvas.height = scaled_viewport.height;

        this.$el.style.width = `${Math.floor(scaled_viewport.width + 1)}px`;
        this.$el.style.height = `${Math.floor(scaled_viewport.height + 1)}px`;

        const render_context = {
          canvasContext: this.$refs.canvas.getContext("2d"),
          viewport: scaled_viewport,
        };

        this.pdfjs_page.render(render_context).promise.then(() => {
          this.$emit("rendered");
        });
      },
    },
  };
</script>

<style scoped>
  .ac-esign-thumbnail {
    position: relative;
  }

  .ac-esign-thumbnail_index {
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 22px;
    height: 21px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #7a96bd45;
    border: 1px solid #c7c7c7;
    border-radius: 50%;
  }

  .ac-esign-thumbnail canvas {
    border: 1.6px solid;
    border-color: #1e3a5c47;
  }
</style>
